@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap");
body,
html {
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  color: #16192c;
  padding: 0;
  background: #fafafe;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
  font-weight: 300;
}

ul,
li,
ol {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
ol,
body,
html {
  margin: 0;
  padding: 0;
}

button {
  background: transparent;
  border: none;
  font-size: 16px;
  color: #16192c;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;
}

img {
  width: 100%;
}

input::placeholder {
  color: #737a91;
  opacity: 0.64;
}

.hidden {
  display: none !important;
}

@font-face {
  font-family: "Menlo Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Menlo Regular"), url("./fonts/Menlo-Regular.woff") format("woff");
}
h2 {
  font-weight: bold;
  font-size: 27px;
  line-height: 32px;
  letter-spacing: 0.01em;
  margin-bottom: 8px;
  width: 100%;
}

p.subtitle {
  font-size: 16px;
  line-height: 24px;
  color: #737a91;
  max-width: 671px;
  margin-bottom: 36px;
  color: #ffffff;
  text-align: center;
  max-width: 590px;
  margin: auto;
  margin-bottom: 40px;
}

.nuxt-content h2,
.nuxt-content h3,
.nuxt-content h4,
.nuxt-content h5,
.nuxt-content h6:not(:first-of-type) {
  margin: 40px 0 5px;
}
.nuxt-content h1 {
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: 0.01em;
}
.nuxt-content h2 {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.01em;
}
.nuxt-content h3 {
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 0.01em;
  margin-top: 30px;
}
.nuxt-content h4 {
  font-weight: bold;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0.01em;
}
.nuxt-content p {
  margin: 15px 0 5px;
  font-size: 16px;
  line-height: 27px;
}
.nuxt-content code {
  background: #d9d9e1;
  border-radius: 4px;
  padding: 2px 4px;
  margin: 0 1px;
  font-family: "Menlo Regular";
  font-size: 14px;
  line-height: 24px;
  color: #000624;
}
.nuxt-content strong {
  font-weight: 500;
}
.nuxt-content a {
  text-decoration: underline;
  color: #47b38d;
}
.nuxt-content img {
  margin: 20px 0 10px;
  border: 1px solid #eee;
  border-radius: 10px;
}
.nuxt-content ul li {
  border-top: 1px solid #e3e8ee;
  padding: 16px 0;
}
.nuxt-content ul li:first-of-type {
  margin-top: 20px;
}
.nuxt-content ol {
  margin-left: 20px;
}
.nuxt-content ol li {
  list-style-type: decimal;
  padding: 8px 0;
}
.nuxt-content hr {
  height: 0;
  border: 1px solid #eee;
  margin: 20px 0 30px;
}

pre[class*=language-] {
  padding: 16px;
  font-size: 13px;
  line-height: 20px;
  background: #f3f3f8;
  border-radius: 4px;
  color: #16192c;
  margin-top: 10px;
  margin-bottom: 4px;
  overflow: auto;
}
pre[class*=language-] code {
  font-family: "Menlo Regular";
  font-size: 13px;
  line-height: 20px;
  font-weight: normal;
  color: rgba(22, 25, 44, 0.5);
  background: transparent;
  border-radius: 0;
  padding: 0;
  margin: 0;
  border: none;
}

.language-yml .token.string, .language-yml .token.number {
  color: rgba(22, 25, 44, 0.5);
}

.token {
  color: #16192c;
  font-family: "Menlo Regular";
  font-size: 13px;
  line-height: 20px;
  font-weight: normal;
}
.token.operator {
  background: transparent;
  color: #16192c;
}
.token.property {
  color: #16192c;
  font-weight: 300;
}
.token.punctuation {
  color: rgba(22, 25, 44, 0.3);
}
.token.string {
  color: #067ab8;
}
.token.number {
  color: #0618b8;
}

.filename,
.code-snippet {
  font-family: "Menlo Regular";
  padding: 8px 12px;
  background: #ececf4;
  border-radius: 4px 4px 0px 0px;
  font-size: 13px;
  margin-top: 30px;
  display: block;
}

.code-snippet {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.code-snippet--method {
  margin-right: 10px;
  font-weight: 500;
}
.code-snippet--method.post {
  color: #09825d;
}
.code-snippet--method.get {
  color: #067ab8;
}
.code-snippet--method.delete {
  color: #e56f4a;
}
.code-snippet img {
  width: 16px;
  margin-right: 8px;
}
.code-snippet--url {
  font-size: 13px;
  opacity: 0.6;
}
.code-snippet--details {
  display: flex;
  align-items: center;
}

footer {
  background: #1e252b;
  padding: 48px 20px 32px;
  color: #ffffff;
}
footer .logo p {
  text-align: left;
  margin-top: 10px;
  opacity: 0.3;
}
footer .logo p a {
  font-weight: bold;
  text-decoration: underline;
}
footer .container {
  max-width: 1376px;
  width: 100%;
  margin: auto;
}
footer nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 29px;
}
footer p {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-top: 24px;
  text-align: center;
}
@media (min-width: 880px) {
  footer p {
    text-align: right;
  }
}
footer .socials li {
  background: rgba(255, 255, 255, 0.1);
}

.socials {
  display: flex;
}
.socials li {
  width: 42px;
  height: 42px;
  background: rgba(49, 50, 61, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.socials li:not(:last-of-type) {
  margin-right: 16px;
}

.newsletter {
  display: flex;
  max-width: 970px;
  padding: 32px 24px;
  margin-top: 40px;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 880px) {
  .newsletter {
    padding: 32px 70px;
    margin-top: 48px;
    flex-direction: row;
    justify-content: space-around;
  }
}
.newsletter > img {
  width: 180px;
  order: 1;
  margin: auto;
}
@media (min-width: 880px) {
  .newsletter > img {
    order: 2;
    margin: initial;
  }
}
.newsletter h5 {
  margin-bottom: 10px;
}
.newsletter h5,
.newsletter p {
  font-size: 14px;
  line-height: 22px;
  font-style: normal;
  font-weight: normal;
  text-align: center;
}
@media (min-width: 880px) {
  .newsletter h5,
.newsletter p {
    text-align: left;
  }
}
.newsletter div {
  max-width: 430px;
  order: 2;
  margin: 0 auto;
}
@media (min-width: 880px) {
  .newsletter div {
    order: 1;
    margin: 0;
  }
}
.newsletter form {
  background: #f5f5fa;
  border: 1px solid #e6eaee;
  box-sizing: border-box;
  border-radius: 8px;
  display: flex;
  padding: 10px 11px;
  margin-top: 25px;
}
.newsletter form img {
  width: 32px;
}
.newsletter form input {
  width: 100%;
  background: transparent;
  border: none;
  outline: none;
}
.newsletter form button img {
  width: 24px;
}

aside {
  width: 240px;
  display: none;
}
@media (min-width: 880px) {
  aside {
    display: block;
    padding-left: 20px;
    padding-right: 32px;
  }
}
aside h3 {
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  color: #000624;
  margin-bottom: 19px;
}
aside ul {
  padding: 0;
}
aside ul li {
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 30px;
  color: #5f5f68;
}
aside form {
  background: #ffffff;
  border: 1px solid rgba(115, 122, 145, 0.16);
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  padding: 11px 0 11px 17px;
  display: flex;
}
aside form img {
  width: 15px;
  margin-right: 10px;
}
aside form input {
  border: none;
  width: 100%;
  outline: none;
}
aside .social {
  margin-top: 100px;
}
aside .social li {
  background: rgba(115, 122, 145, 0.1);
}

.posts {
  gap: 48px;
  max-width: 970px;
  margin-top: 10px;
}
.posts.featured {
  gap: 0 0;
}
.posts .post {
  width: 100%;
  background: #ffffff;
  box-shadow: 40px 44px 81px rgba(111, 118, 138, 0.08);
  border-radius: 8px;
  padding: 10px 10px 23px;
}
.posts .post .tag {
  padding: 3px 16px;
  background: rgba(7, 71, 166, 0.1);
  border-radius: 2px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #477db3;
  text-transform: uppercase;
}
.posts .post .tag.clear {
  background: transparent;
  padding: 0;
}
.posts .post--head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}
.posts .post .date {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}
.posts .post--body {
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  color: #31323d;
  margin: 0 15px 20px;
}
.posts .post--body.single-feature {
  margin: 0 0 16px;
}
.posts .post--title {
  color: #16192c;
  margin: 0 15px 16px;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
}
.posts .post--title.single-feature {
  margin: 0 0 16px;
}
.posts .post--title.small {
  font-size: 21px;
}
.posts .post--footer {
  margin: 18px 15px 26px;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width: 880px) {
  .posts .post--footer {
    align-items: flex-end;
    flex-flow: row;
  }
}
.posts .post--footer.single-feature {
  margin: 27px 0 26px;
}
.posts .post--footer a {
  display: flex;
  align-items: center;
  color: #477db3;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}
.posts .post--footer a img {
  width: 20px;
}
.posts .post--author {
  display: flex;
  align-items: flex-start;
  margin-bottom: 40px;
}
@media (min-width: 880px) {
  .posts .post--author {
    margin-bottom: unset;
  }
}
.posts .post--author .img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #f5f5f5;
  margin-right: 16px;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.posts .post--author img {
  width: 100% !important;
  border-radius: 50%;
  margin-right: 12px;
}
.posts .post--author h5 {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 3px;
}
.posts .post--author p {
  font-size: 14px;
  line-height: 20px;
  color: #31323d;
}
.posts .post--img {
  border-radius: 4px;
  margin-bottom: 21px;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  backdrop-filter: blur(3.4767px);
  height: 171px;
}
@media (min-width: 880px) {
  .posts .post--img {
    margin-bottom: 41px;
  }
}
.posts .post--img img {
  border-radius: 4px;
  width: 100%;
}

.tabs {
  background: rgba(252, 252, 252, 0.16);
  border-radius: 8.91px;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  flex-flow: row;
  margin-bottom: 55px;
}
.tabs li button {
  border-radius: 6.93px;
  padding: 13px 8px;
}
.tabs li button span {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #fcfcfc;
  min-width: 86px;
  display: inline-block;
}
.tabs li button.active {
  transition: 0.3s ease-in-out;
  background: #ffffff;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.12), 0px 3px 1px rgba(0, 0, 0, 0.04);
}
.tabs li button.active span {
  font-weight: 600;
  transition: 0.3s ease-in-out;
  color: #000624;
}
@media (min-width: 880px) {
  .tabs li button span {
    font-size: 14px;
    line-height: 16px;
    min-width: 132px;
  }
  .tabs li button.active span {
    font-weight: 600;
  }
}
.tabs__light {
  background: #ececf4;
}
.tabs__light li button span {
  color: #737a91;
}
.tabs__line {
  background: none;
  border-radius: 0;
  width: 100%;
  margin: 0;
  margin-bottom: 24px;
  border-bottom: 1px solid #EDEFF5;
}
.tabs__line li:not(:last-of-type) {
  margin-right: 25px;
}
.tabs__line li button {
  border-radius: 0;
  padding: 0;
  padding-bottom: 10px;
}
.tabs__line li button span {
  font-size: 14px;
  line-height: 24px;
  text-align: left;
  color: #000624;
}
@media (min-width: 880px) {
  .tabs__line li button span {
    min-width: 80px;
  }
}
.tabs__line li button.has-icon {
  display: flex;
  align-items: center;
}
.tabs__line li button.has-icon img {
  height: 16px;
  width: 16px;
  margin: unset;
  border-radius: unset;
  border: unset;
  margin-right: 16px;
}
.tabs__line li button.active {
  transition: 0.3s ease-in-out;
  background: none;
  box-shadow: none;
  position: relative;
}
.tabs__line li button.active span {
  font-weight: 600;
  transition: 0.3s ease-in-out;
  color: #477db3;
}
.tabs__line li button.active::after {
  content: "";
  bottom: 0px;
  height: 3px;
  width: 100%;
  left: 0;
  right: 0;
  background: #477db3;
  border-radius: 16px 16px 0px 0px;
  position: absolute;
}

.margin-top__32px {
  margin-top: 32px;
}

.blog-page .main {
  margin: 0 auto;
  padding-bottom: 0;
  display: flex;
  justify-content: space-between;
  height: unset;
  padding-top: 0;
}
.blog-page main {
  max-width: 1035px;
  width: 100%;
  padding: 0 20px;
}
.blog-page main h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 35px;
  color: #000624;
  margin-bottom: 1px;
  display: flex;
  align-items: center;
}
.blog-page main h1 button {
  height: fit-content;
  margin-top: 5px;
  margin-left: 8px;
}
@media (min-width: 880px) {
  .blog-page main h1 button {
    display: none;
  }
}
.blog-page main > p {
  font-size: 16px;
  line-height: 24px;
  color: #5f5f68;
}
.blog-page .card {
  background: #ffffff;
  box-shadow: 10px 20px 81px rgba(111, 118, 138, 0.08);
  border-radius: 8px;
}
.blog-page .dropdown-container {
  position: relative;
}
.blog-page .dropdown-container .dropdown {
  position: absolute;
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(12, 26, 75, 0.04), 0px 4px 20px -2px rgba(50, 50, 71, 0.08);
  border-radius: 10px;
  padding: 24px;
  z-index: 1;
  width: 217px;
  margin-top: 4px;
}
.blog-page .dropdown-container .dropdown li {
  margin: 0 0 32px;
  font-size: 14px;
  line-height: 22px;
  color: #5f5f68;
}
.blog-page .dropdown-container .dropdown li:last-of-type {
  margin-bottom: 0;
}

.input {
  display: flex;
  flex-flow: column;
  position: relative;
}
.input input,
.input select {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #fcfcfc;
  background: #181d31;
  border: 1px solid rgba(115, 122, 145, 0.46);
  border-radius: 4px;
  padding: 10px 16px;
  transition: all 0.3s;
}
.input input:focus,
.input select:focus {
  border: 1px solid rgba(115, 122, 145, 0.46);
  outline: none;
  padding: 20px 16px 10px 15px;
}
.input input:valid,
.input select:valid {
  padding: 20px 16px 10px 15px;
}
.input select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent url(../assets/images/angle-down-icon.svg) no-repeat;
  background-position-x: 94%;
  background-position-y: 18px;
}
.input label {
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  color: #a5abc1;
  text-transform: capitalize;
  position: absolute;
  margin-left: 16px;
  top: 9px;
  transition: 0.3s ease-in-out all;
}
.input input:valid + label,
.input select:valid + label,
.input input:-internal-autofill-selected + label {
  font-weight: 500;
  font-size: 10px;
  margin-left: 16px;
  top: 6px;
}
.input__search {
  display: flex;
  align-items: center;
  background: #ffffff;
  border: 1px solid rgba(115, 122, 145, 0.16);
  box-sizing: border-box;
  border-radius: 4px;
  padding: 11px 16px;
  width: 100%;
}
.input__search img {
  width: 18px;
}
.input__search input {
  border: none;
  margin-left: 10px;
  width: 100%;
  outline: none;
}